<template>
	<view class="news-edit-container">
		<!-- È°∂ÈÉ®ÂØºËà™Ê†è -->
		<view class="nav-bar" :style="{ paddingTop: statusBarHeight + 'px' }">
			<view class="nav-content">
				<view class="back-btn" @click="goBack">
					<image class="back-icon" src="/static/back-cartoon.svg" mode="aspectFit" />
				</view>
				<text class="nav-title">{{ isEdit ? 'ÁºñËæë' : 'ÂèëÂ∏É' }}</text>
				<view class="nav-right"></view>
			</view>
		</view>

		<!-- ÁºñËæëË°®Âçï -->
		<view class="edit-form" :style="{ paddingTop: (statusBarHeight + 44) + 'px', paddingBottom: (safeAreaBottom + 120) + 'px' }">
			<view class="form-group">
				<text class="form-label">Ê†áÈ¢ò *</text>
				<input 
					class="form-input" 
					v-model="newsForm.title" 
					placeholder="ËØ∑ËæìÂÖ•Ê†áÈ¢ò"
					maxlength="100"
				/>
			</view>

			<view class="form-group">
				<text class="form-label">ÂèëÂ∏ÉÊó∂Èó¥</text>
				<picker 
					mode="date" 
					:value="newsForm.time" 
					@change="onDateChange"
					class="date-picker"
				>
					<view class="picker-display">
						<text>{{ newsForm.time || 'ËØ∑ÈÄâÊã©Êó•Êúü' }}</text>
						<text class="picker-arrow">></text>
					</view>
				</picker>
			</view>

			<view class="form-group">
				<text class="form-label">‰ΩúËÄÖ</text>
				<input 
					class="form-input" 
					v-model="newsForm.author" 
					placeholder="ËØ∑ËæìÂÖ•‰ΩúËÄÖÂßìÂêç"
					maxlength="50"
				/>
			</view>

			<view class="form-group">
				<view class="switch-container">
					<text class="form-label">ÁΩÆÈ°∂</text>
					<switch 
						:checked="newsForm.isPinned" 
						@change="onPinChange"
						color="#007aff"
					/>
				</view>
				<text class="switch-tip">ÂºÄÂêØÂêéÔºåÊ≠§‰∫ã‰ª∂Â∞ÜÊòæÁ§∫Âú®ÂàóË°®È°∂ÈÉ®</text>
			</view>

			<view class="form-group">
				<text class="form-label">ÂÜÖÂÆπ *</text>
				<view class="content-editor">
					<view class="editor-toolbar">
						<button class="tool-btn" @click="addTextBlock">
							<text class="tool-icon">üìù</text>
							<text class="tool-text">Ê∑ªÂä†ÊñáÂ≠ó</text>
						</button>
						<button class="tool-btn" @click="addImageBlock">
							<text class="tool-icon">üñºÔ∏è</text>
							<text class="tool-text">Ê∑ªÂä†ÂõæÁâá</text>
						</button>
					</view>
					
					<view class="content-blocks">
						<view 
							v-for="(block, index) in newsForm.contentBlocks" 
							:key="index"
							class="content-block"
						>
							<!-- ÊñáÂ≠óÂùó -->
							<view v-if="block.type === 'text'" class="text-block">
								<textarea 
									class="text-input" 
									v-model="block.content" 
									placeholder="ËØ∑ËæìÂÖ•ÊñáÂ≠óÂÜÖÂÆπ"
									maxlength="1000"
									@input="updateCharCount"
								/>
								<view class="block-actions">
									<text class="char-count">{{ block.content.length }}/1000</text>
									<button class="action-btn delete" @click="removeBlock(index)">
										Âà†Èô§
									</button>
									<button class="action-btn move-up" @click="moveBlock(index, -1)" :disabled="index === 0">
										‰∏äÁßª
									</button>
									<button class="action-btn move-down" @click="moveBlock(index, 1)" :disabled="index === newsForm.contentBlocks.length - 1">
										‰∏ãÁßª
									</button>
								</view>
							</view>
							
							<!-- ÂõæÁâáÂùó -->
							<view v-else-if="block.type === 'image'" class="image-block">
								<view class="image-preview">
									<image 
										:src="block.imageUrl" 
										class="preview-image" 
										mode="aspectFill"
										@click="previewImage(block.imageUrl)"
									/>
									<button class="remove-image-btn" @click="removeBlock(index)">√ó</button>
								</view>
								<view class="image-caption">
									<input 
										class="caption-input" 
										v-model="block.caption" 
										placeholder="ÂõæÁâáËØ¥ÊòéÔºàÂèØÈÄâÔºâ"
										maxlength="100"
									/>
								</view>
								<view class="block-actions">
									<button class="action-btn move-up" @click="moveBlock(index, -1)" :disabled="index === 0">
										‰∏äÁßª
									</button>
									<button class="action-btn move-down" @click="moveBlock(index, 1)" :disabled="index === newsForm.contentBlocks.length - 1">
										‰∏ãÁßª
									</button>
								</view>
							</view>
						</view>
						
						<view v-if="newsForm.contentBlocks.length === 0" class="empty-content">
							<text>ÁÇπÂáª‰∏äÊñπÊåâÈíÆÊ∑ªÂä†ÂÜÖÂÆπÂùó</text>
						</view>
					</view>
				</view>
			</view>

			<!-- Â∫ïÈÉ®Âõ∫ÂÆö‰øùÂ≠òÊ†è -->
			<view class="bottom-bar" :style="{ paddingBottom: safeAreaBottom + 'px' }">
				<button class="bottom-save-btn" @click="saveNews" :disabled="!canSave || saving">
					{{ saving ? '‰øùÂ≠ò‰∏≠...' : 'ÂèëÂ∏É' }}
				</button>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			statusBarHeight: 0,
			safeAreaBottom: 0,
			newsId: '',
			isEdit: false,
			saving: false,
			newsForm: {
				title: '',
				content: '',
				time: '',
				author: '',
				isPinned: false, // Êñ∞Â¢ûÔºöÁΩÆÈ°∂Áä∂ÊÄÅ
				contentBlocks: [] // Êñ∞Â¢ûÔºöÁî®‰∫éÂ≠òÂÇ®ÂÜÖÂÆπÂùó
			}
		}
	},
	
	computed: {
		canSave() {
			return this.newsForm.title.trim() && 
				   this.newsForm.contentBlocks.length > 0 && 
				   this.newsForm.time && 
				   !this.saving;
		}
	},
	
	onLoad(options) {
		console.log('Êñ∞ÈóªÁºñËæëÈ°µÈù¢Âä†ËΩΩÔºåÂèÇÊï∞:', options);
		console.log('ÂΩìÂâçÁî®Êà∑‰ø°ÊÅØ:', uni.getStorageSync('userInfo'));
		
		// ÈÄÇÈÖçÁä∂ÊÄÅÊ†èÈ´òÂ∫¶ & ÂÆâÂÖ®Âå∫
		const sys = uni.getSystemInfoSync();
		this.statusBarHeight = sys.statusBarHeight || 20;
		let bottomInset = 0;
		if (sys.safeAreaInsets && typeof sys.safeAreaInsets.bottom === 'number') {
			bottomInset = sys.safeAreaInsets.bottom;
		} else if (sys.safeArea && typeof sys.safeArea.bottom === 'number') {
			bottomInset = sys.screenHeight - sys.safeArea.bottom;
		}
		this.safeAreaBottom = bottomInset || 0;
		
		// Ê£ÄÊü•Áî®Êà∑ÊùÉÈôê
		const userInfo = uni.getStorageSync('userInfo');
		if (!userInfo || !(userInfo.permission === 'ÁÆ°ÁêÜÂëò' || userInfo.permission === 'superadmin')) {
			console.log('ÊùÉÈôêÊ£ÄÊü•Â§±Ë¥•:', { userInfo, permission: userInfo?.permission });
			uni.showModal({
				title: 'ÊùÉÈôê‰∏çË∂≥',
				content: 'Âè™ÊúâÁÆ°ÁêÜÂëòÂèØ‰ª•ÁºñËæëÊñ∞ÈóªÔºåËØ∑‰ΩøÁî®ÁÆ°ÁêÜÂëòË¥¶Âè∑ÁôªÂΩï',
				showCancel: false,
				success: () => {
					uni.navigateBack();
				}
			});
			return;
		}
		
		console.log('ÊùÉÈôêÊ£ÄÊü•ÈÄöËøáÔºåÁªßÁª≠Âä†ËΩΩÈ°µÈù¢');
		
		// ËÆæÁΩÆÈªòËÆ§Êó∂Èó¥
		this.newsForm.time = new Date().toISOString().split('T')[0];
		
		if (options.id) {
			this.newsId = options.id;
			this.isEdit = true;
			this.loadNewsData();
		}
	},
	
	methods: {
		goBack() {
			uni.navigateBack();
		},
		
		async loadNewsData() {
			try {
				// ‰ºòÂÖà‰ªé‰∫ëÁ´ØËé∑Âèñ
				if (this.newsId) {
					const res = await uniCloud.callFunction({
						name: 'news-service',
						data: { action: 'getNewsById', id: this.newsId }
					});
					if (res && res.result && res.result.code === 0 && res.result.data) {
						const news = res.result.data;
						this.newsForm = {
							title: news.title || '',
							content: news.content || '',
							time: news.time || new Date().toISOString().split('T')[0],
							author: news.author || '',
							isPinned: news.isPinned || false,
							contentBlocks: Array.isArray(news.contentBlocks) ? news.contentBlocks : [{ type: 'text', content: news.content || '' }]
						};
						return;
					}
				}
				// ÂõûÈÄÄÂà∞Êú¨Âú∞Â≠òÂÇ®
				await this.loadNewsFromStorage();
			} catch (error) {
				console.error('Âä†ËΩΩÊñ∞ÈóªÊï∞ÊçÆÂ§±Ë¥•:', error);
				uni.showToast({ title: 'Âä†ËΩΩÂ§±Ë¥•', icon: 'none' });
			}
		},
		
		async loadNewsFromStorage() {
			// ‰ªéÊú¨Âú∞Â≠òÂÇ®Ëé∑ÂèñÊñ∞ÈóªÂàóË°®
			const newsList = uni.getStorageSync('newsList') || [];
			const news = newsList.find(item => item.id === this.newsId);
			
			if (news) {
				// Â¶ÇÊûúÊâæÂà∞Êñ∞ÈóªÔºå‰ΩøÁî®Â≠òÂÇ®ÁöÑÊï∞ÊçÆ
				this.newsForm = {
					title: news.title || '',
					content: news.content || '',
					time: news.time || new Date().toISOString().split('T')[0],
					author: news.author || '',
					isPinned: news.isPinned || false,
					contentBlocks: news.contentBlocks || [
						{ type: 'text', content: news.content || '' }
					]
				};
			} else {
				// Â¶ÇÊûúÊ≤°ÊâæÂà∞Ôºå‰ΩøÁî®Ê®°ÊãüÊï∞ÊçÆ
				await this.loadMockNewsData();
			}
		},
		
		async loadMockNewsData() {
			// Ê®°ÊãüÂä†ËΩΩÊñ∞ÈóªÊï∞ÊçÆ
			await new Promise(resolve => setTimeout(resolve, 500));
			
			// Ê®°ÊãüÊñ∞ÈóªÊï∞ÊçÆ
			//this.newsForm = {
				//title: 'ÂÖ¨Âè∏Ëç£Ëé∑XXÂ•ñÈ°π11',
				//content: 'ËøëÊó•ÔºåÂÖ¨Âè∏Âú®ÊïôËÇ≤È¢ÜÂüüÂèñÂæó‰∫ÜÈáçÂ§ßÁ™ÅÁ†¥ÔºåËç£Ëé∑XXÂ•ñÈ°π„ÄÇËøôÊòØÂØπÊàë‰ª¨‰∏ìÊ≥®ÊïôËÇ≤„ÄÅÁÇπ‰∫ÆÊàêÈïøÁêÜÂøµÁöÑËÇØÂÆö„ÄÇ',
				//time: '2024-06-01',
				//author: 'Êñ∞Ê¨£ÁÅ´ÊïôËÇ≤‰∏≠ÂøÉ',
				//contentBlocks: [
				//	{ type: 'text', content: 'ËøëÊó•ÔºåÂÖ¨Âè∏Âú®ÊïôËÇ≤È¢ÜÂüüÂèñÂæó‰∫ÜÈáçÂ§ßÁ™ÅÁ†¥ÔºåËç£Ëé∑XXÂ•ñÈ°π„ÄÇËøôÊòØÂØπÊàë‰ª¨‰∏ìÊ≥®ÊïôËÇ≤„ÄÅÁÇπ‰∫ÆÊàêÈïøÁêÜÂøµÁöÑËÇØÂÆö„ÄÇ' }
				//]
			//};
		},
		
		onDateChange(e) {
			this.newsForm.time = e.detail.value;
		},

		onPinChange(e) {
			this.newsForm.isPinned = e.detail.value;
		},

		addTextBlock() {
			this.newsForm.contentBlocks.push({ type: 'text', content: '' });
		},

		addImageBlock() {
			// Ê£ÄÊü•Áî®Êà∑ÊùÉÈôê
			const userInfo = uni.getStorageSync('userInfo');
			if (!userInfo || !(userInfo.permission === 'ÁÆ°ÁêÜÂëò' || userInfo.permission === 'superadmin')) {
				uni.showToast({ title: 'ÊùÉÈôê‰∏çË∂≥', icon: 'none' });
				return;
			}
			
			uni.chooseImage({
				count: 1,
				sizeType: ['compressed'],
				sourceType: ['album', 'camera'],
				success: (res) => {
					console.log('ÈÄâÊã©ÂõæÁâáÊàêÂäü:', res);
					
					const filePath = res.tempFilePaths[0];
					
					// ÊòæÁ§∫‰∏ä‰º†‰∏≠ÊèêÁ§∫
					uni.showLoading({ title: '‰∏ä‰º†ÂõæÁâá‰∏≠...' });
					
					// ‰∏ä‰º†ÂõæÁâáÂà∞‰∫ëÂ≠òÂÇ®
					this.uploadImageToCloud(filePath);
				},
				fail: (err) => {
					console.error('ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•:', err);
					if (err.errMsg && err.errMsg.includes('cancel')) {
						return;
					}
					uni.showToast({ title: 'ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•', icon: 'none' });
				}
			});
		},
		
		async uploadImageToCloud(filePath) {
			try {
				const cloudPath = `news-images/${Date.now()}_${Math.random().toString(36).substring(2,10)}.jpg`;
				const uploadRes = await uniCloud.uploadFile({ filePath, cloudPath });
				const cloudUrl = uploadRes.fileID;
				
				// Ê∑ªÂä†ÂõæÁâáÂùóÂà∞ÂÜÖÂÆπ
				this.newsForm.contentBlocks.push({ 
					type: 'image', 
					imageUrl: cloudUrl, 
					caption: '' 
				});
				
				uni.hideLoading();
				uni.showToast({ title: 'ÂõæÁâá‰∏ä‰º†ÊàêÂäü', icon: 'success' });
				console.log('ÂΩìÂâçcontentBlocks:', this.newsForm.contentBlocks);
				
			} catch (error) {
				console.error('ÂõæÁâá‰∏ä‰º†Â§±Ë¥•:', error);
				uni.hideLoading();
				uni.showToast({ title: 'ÂõæÁâá‰∏ä‰º†Â§±Ë¥•', icon: 'none' });
			}
		},
		
		// ÁßªÈô§Ê®°Êãü‰∏ä‰º†ÂáΩÊï∞

		removeBlock(index) {
			this.newsForm.contentBlocks.splice(index, 1);
		},

		moveBlock(fromIndex, toIndex) {
			const block = this.newsForm.contentBlocks.splice(fromIndex, 1)[0];
			this.newsForm.contentBlocks.splice(toIndex, 0, block);
		},

		updateCharCount() {
			// Êõ¥Êñ∞ÊØè‰∏™ÊñáÂ≠óÂùóÁöÑÂ≠óÁ¨¶ËÆ°Êï∞
			this.newsForm.contentBlocks.forEach(block => {
				if (block.type === 'text') {
					block.charCount = block.content.length;
				}
			});
		},

		previewImage(url) {
			// Â¶ÇÊûúÊòØ‰∏¥Êó∂ÂõæÁâáË∑ØÂæÑÔºåÈúÄË¶ÅÁâπÊÆäÂ§ÑÁêÜ
			if (url && url.startsWith('http://tmp/')) {
				// ‰∏¥Êó∂ÂõæÁâáÔºåÁõ¥Êé•È¢ÑËßà
				uni.previewImage({
					urls: [url],
					current: url
				});
			} else if (url) {
				// ÊôÆÈÄöÂõæÁâá
				uni.previewImage({
					urls: [url],
					current: url
				});
			}
		},
		
		async saveNews() {
			if (!this.canSave) return;
			
			console.log('ÂºÄÂßã‰øùÂ≠òÊñ∞ÈóªÔºåÂΩìÂâçÊï∞ÊçÆ:', this.newsForm);
			
			// È™åËØÅÂÜÖÂÆπÂùó
			for (let block of this.newsForm.contentBlocks) {
				if (block.type === 'text' && !block.content.trim()) {
					uni.showToast({ title: 'ËØ∑Â°´ÂÜôÂÆåÊï¥ÁöÑÊñáÂ≠óÂÜÖÂÆπ', icon: 'none' });
					return;
				}
				if (block.type === 'image' && !block.imageUrl) {
					uni.showToast({ title: 'ËØ∑ÈÄâÊã©ÂõæÁâá', icon: 'none' });
					return;
				}
			}
			
			try {
				this.saving = true;
				
				// ÁîüÊàêÊúÄÁªàÂÜÖÂÆπÔºàÁî®‰∫éÂÖºÂÆπÊóßÁâàÊú¨Ôºâ
				const finalContent = this.newsForm.contentBlocks
					.map(block => {
						if (block.type === 'text') {
							return block.content;
						}
						// ÂõæÁâáÂùó‰∏çÁîüÊàêÂç†‰ΩçÁ¨¶ÔºåÁõ¥Êé•Ë∑≥Ëøá
						return '';
					})
					.filter(content => content.trim()) // ËøáÊª§ÊéâÁ©∫ÂÜÖÂÆπ
					.join('\n\n');
				
				console.log('ÁîüÊàêÁöÑÊúÄÁªàÂÜÖÂÆπ:', finalContent);
				console.log('‰øùÂ≠òÁöÑÂÆåÊï¥Êï∞ÊçÆ:', {
					...this.newsForm,
					content: finalContent,
					contentBlocks: this.newsForm.contentBlocks
				});
				
				// ‰øùÂ≠òÂà∞‰∫ëÁ´Ø
				const payload = { ...this.newsForm, content: finalContent, contentBlocks: this.newsForm.contentBlocks };
				if (this.isEdit) {
					await uniCloud.callFunction({ name: 'news-service', data: { action: 'updateNews', id: this.newsId, data: payload } });
				} else {
					const saveRes = await uniCloud.callFunction({ name: 'news-service', data: { action: 'createNews', data: payload } });
					if (saveRes && saveRes.result && saveRes.result.code === 0 && saveRes.result.data && saveRes.result.data._id) {
						this.newsId = saveRes.result.data._id;
					}
				}
				
				uni.showToast({ 
					title: this.isEdit ? '‰øÆÊîπÊàêÂäü' : 'ÂèëÂ∏ÉÊàêÂäü', 
					icon: 'success' 
				});
				
				// ÈÄöÁü•È¶ñÈ°µÂà∑Êñ∞
				uni.$emit('newsUpdated');
				setTimeout(() => { uni.navigateBack(); }, 1200);
				
			} catch (error) {
				console.error('‰øùÂ≠òÊñ∞ÈóªÂ§±Ë¥•:', error);
				uni.showToast({ title: '‰øùÂ≠òÂ§±Ë¥•', icon: 'none' });
			} finally {
				this.saving = false;
			}
		}
	}
}
</script>

<style scoped>
.news-edit-container {
	min-height: 100vh;
	background: #f8f8f8;
}

.nav-bar {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	background: #fff;
	z-index: 1000;
	box-shadow: 0 2rpx 10rpx rgba(0,0,0,0.1);
}

.nav-content {
	height: 44px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 20rpx;
}

.back-btn {
	width: 60rpx;
	height: 60rpx;
	display: flex;
	align-items: center;
	justify-content: center;
}

.back-icon {
	width: 40rpx; /* Adjust as needed */
	height: 40rpx; /* Adjust as needed */
}

.nav-title {
	font-size: 32rpx;
	font-weight: bold;
	color: #333;
}

.save-btn {
	background: #007aff;
	color: #fff;
	border: none;
	border-radius: 20rpx;
	padding: 8rpx 24rpx;
	font-size: 28rpx;
}

.save-btn:disabled {
	background: #ccc;
}

.edit-form {
	padding: 20rpx;
}

/* Â∫ïÈÉ®Âõ∫ÂÆö‰øùÂ≠òÊ†è */
.bottom-bar {
	position: fixed;
	left: 0;
	right: 0;
	bottom: 0;
	background: #fff;
	padding: 16rpx 20rpx;
	box-shadow: 0 -2rpx 10rpx rgba(0,0,0,0.08);
	z-index: 1000;
}

.bottom-save-btn {
	width: 100%;
	background: #007aff;
	color: #fff;
	border: none;
	border-radius: 24rpx;
	padding: 22rpx 0;
	font-size: 32rpx;
}

.bottom-save-btn:disabled {
	background: #ccc;
}

.form-group {
	background: #fff;
	border-radius: 16rpx;
	padding: 30rpx;
	margin-bottom: 20rpx;
	box-shadow: 0 2rpx 10rpx rgba(0,0,0,0.05);
}

.form-label {
	font-size: 30rpx;
	font-weight: bold;
	color: #333;
	margin-bottom: 20rpx;
